import React from 'react'
import { graphql, useStaticQuery } from "gatsby"

const query = graphql`
    query {
        allContentfulPricing {
            nodes {
                header
                helpText {
                    helpText
                }
                iban
                mbWay1
                mbWay2
                additionalText {
                    additionalText
                }
            }
        }
        allContentfulPricingPack {
            nodes {
              header
              currency
              price
              pricePer
              description
              packLines {
                description
              }
              image {
                  file {
                      url
                  }
              }
              href
            }
          }
    }
`

const Pricing = () => {
    const pricingData = useStaticQuery(query).allContentfulPricing.nodes
    const packs = useStaticQuery(query).allContentfulPricingPack.nodes

    return (
            <section className="pricing-area ptb-100 bg-f4f7fe">
                <div className="container">
                    <div className="section-title">
                        <h2>{pricingData[0].header}</h2>
                        <p>{pricingData[0].helpText.helpText}</p>
                        <p>{pricingData[0].iban}</p>
                        <p>{pricingData[0].mbWay1}</p>
                        <p>{pricingData[0].mbWay2}</p>
                        <p>{pricingData[0].additionalText.additionalText}</p>
                    </div>
                    <div className='row'>
                        {packs.map(p => (
                            <div className="col-lg-4 col-md-6">
                                <div className="single-pricing-box">
                                    <div className='pricing-image'>
                                        <img src={p.image.file.url} alt="pricing" />
                                    </div>
                                    
                                    <div className="pricing-header">
                                        <h3>{p.header}</h3>
                                    </div>
                
                                    <div className="price">
                                        <sup>{p.currency}</sup> {p.price} <sub> {p.pricePer} </sub>
                                    </div>
                
                                    <ul className="pricing-features">
                                        {p.packLines.map(d => (
                                            <li><i className='bx bxs-badge-check'></i> {d.description} </li>
                                        ))}
                                    </ul>
                                    <div className="box-wrapper">
                                        <div className="btn-box">
                                            <a href={p.href} className="default-btn">
                                                <i className="flaticon-tick"></i> 
                                                    Selecione Este Pack
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </section>
        )
}

export default Pricing
